*{
  margin:0;
  padding:0;
}
.container{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:#344151;
  flex-direction: column;
  gap: 2em;

}
.buttons{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  font-size:2.5em;
}

.heading{
  color: #0398d4;
}

.reset button{
   background-color:#0398d4;
   font-size:1.5em;
   color:white;
   border: none;
   border-radius:0.2em;
   padding: 0.3em 0.7em;
}
.neg{
  border: none;
  font-weight: bolder;
  font-size: larger;
  padding: 0 0.5em;
  border-right: 3px solid rgb(143, 143, 161);
}
.pos{
  border: none;
  font-weight: bolder;
  font-size: larger;
  padding: 0 0.5em;
  border-left: 3px solid rgb(143, 143, 161);
}
.count{
  padding: 0 0.8em;
  font-weight: bolder;
}